const QUESTIONS_MIN_NUM = 3;
const VARS_MIN_NUM = 2;
const VARS_MAX_NUM = 10;


export default {

    check(testObj) {

        // console.log(testObj);

        let res = {
            status: true,
            msg: '',
            code: 3,
            questionId: undefined
        };

        //     
        for(let prop in testObj) {

            switch (prop) {
                case 'title':
                    res.status = checkTitle(testObj.title);
                    if(!res.status) {
                        res.msg = '            3  200 !      ,        ';
                        res.code = 1;
                        return res;
                    }
                    break;
                case 'description':
                    res.status = checkDesc(testObj.description);
                    if(!res.status) {
                        res.msg = '      1200 .';
                        res.code = 1;
                        return res;
                    }
                    break;
                case 'questions':

                    let check = checkQuestions(testObj.questions);
                    res.status = check.status;
                    if(!res.status) {
                        res.msg = check.msg;
                        res.questionId = check.questionId;
                        res.code = check.code;
                        return res;
                    }
                    break;
                default:

            }
        }

        return res;

    }

}

// Helpers
function checkTitle(title) {
    let pattern = /[-a-z0-9_"]{3,200}/i;
    return pattern.test(title);
}

function checkDesc(desc) {
    let pattern = /.{0,1200}/i;
    return pattern.test(desc);
}

function checkQuestions(questions) {
    let res = {
        status: true,
        msg: '',
        code: 3,
        questionId: undefined
    };

    //       
    if(questions.length <= QUESTIONS_MIN_NUM) {
        res.status = false;
        res.code = 1;
        res.msg = '   ,   4 ';
        return res;
    }

    for(let i = 0; i < questions.length; i++) {
        let check = checkQuestion(questions[i]);
        if(!check.status) {
            res.status = check.status;
            res.code = 1;
            res.msg = check.msg;
            res.questionId = check.questionId;
            return res;
        }
    }

    return res;
}

function checkQuestion(question) {

    let res = {
        status: true,
        questionId: question.id,
        msg: ''
    }
    //    
    if(question.text.length < 10 || question.text.length > 800) {
        res.status = false;
        res.questionId = question.id;
        res.msg = '     .      10   800';
        return res;
    }

    else {

        //         
        if(question.type === 1 || question.type === 2) {

            //   
            if(question.vars.length < VARS_MIN_NUM) {
                res.status = false;
                res.questionId = question.id;
                res.msg = '      ' + VARS_MIN_NUM + '   ';
                return res;
            }

            //   
            else if(question.vars.length > VARS_MAX_NUM) {
                res.status = false;
                res.questionId = question.id;
                res.msg = '     ' + VARS_MAN_NUM;
                return res;
            }

            else if(!checkMultiVarsText(question.vars)) {
                res.status = false;
                res.questionId = question.id;
                res.msg = '           200 ';
                return res;
            }

            else if(!checkMultiVarsRights(question.vars)) {
                res.status = false;
                res.questionId = question.id;
                res.msg = '  ,   1   ';
                return res;
            }


        }

        //    - 
        else if(question.type === 3) {
            if(question.vars.length === 0) {
                res.status = false;
                res.questionId = question.id;
                res.msg = '  ,   1   ';
                return res;
            }

        }
        return res;
    }
}

function checkMultiVarsRights(vars) {
    for(let i = 0; i < vars.length; i++) {
        if(vars[i].isRight) return true;
    }
    return false;
}

function checkMultiVarsText(vars) {
    for(let i = 0; i < vars.length; i++) {
        vars[i].text.length;
        if(vars[i].text.length === 0 || vars[i].text.length > 500) {
            return false;
        }
    }
    return true;
}



